<template>
    <div ref="agriculturalList" class="agriculturalList" @scroll="handleScoll" :key="key">
        <div class="agriculturalList-wrap" ref="agriculturalListWrap">
            <div class="agricultural-item" v-for="(item, key) in list" :key="key">
                <SwipeCell :before-close="beforeSwipeCellClose" :name="key" :disabled="!isEdit || item.operateStatus != agriculturalStatusDict.waitExecute">
                    <div class="agricultural-item-box whiteBg">
                        <div class="agricultural-item-title">
                            <div class="agricultural-item-name">{{ item.farmingName }}</div>
                            <div class="agricultural-item-teaGarden">
                                <img src="@/components/images/agriculturalList/site.png" alt="">
                                <span class="color">西黄山1号茶园</span>
                            </div>
                            <div class="agricultural-item-status">
                                <img v-if="item.operateStatus === agriculturalStatusDict.waitExecute" src="@/components/images/agriculturalList/waitExecuteStatus.png" alt="">
                                <img v-else-if="item.operateStatus === agriculturalStatusDict.execute" src="@/components/images/agriculturalList/executeStatus.png" alt="">
                                <img v-else-if="item.operateStatus === agriculturalStatusDict.complete" src="@/components/images/agriculturalList/completeStatus.png" alt="">
                                <img v-else src="@/components/images/agriculturalList/overdueStatus.png" alt="">
                            </div>
                        </div>

                        <div class="agricultural-item-content">
                            <div class="agricultural-item-row">
                                <img src="@/components/images/agriculturalList/workNumber.png" alt="">
                                <div>
                                    工单编号：{{ item.workNo }}
                                    <div class="agricultural-item-QRCode" @click="handleClickWorkQRCode(item, key)">
                                        <FcCodeToQRCode :ref="`FcCodeToQRCode-${key}`" v-if="item.farmingType == farmingTypeDict.pickTeaGreen && item.operateStatus === agriculturalStatusDict.complete" :code="item.workCodeUrl"></FcCodeToQRCode>
                                    </div>
                                </div>
                            </div>
                            <div class="agricultural-item-row" v-if="isEdit">
                                <img src="@/components/images/taeGreenItem/worker.png" alt="">
                                <div>茶园工：{{ item.operatePersonName }} {{ item.operatePersonPhone }}</div>
                            </div>
                            <div class="agricultural-item-row">
                                <img src="@/components/images/agriculturalList/workDesc.png" alt="">
                                <div>农事描述：{{ item.farmingRemark }}</div>
                            </div>
                            <div class="agricultural-item-row" v-if="item.farmingType === farmingTypeDict.pickTeaGreen">
                                <img src="@/components/images/taeGreenItem/varieties.png" alt="">
                                <div>茶树品种：{{ item.breed }}</div>
                            </div>
                            <div class="agricultural-item-row" v-if="item.operateStatus === agriculturalStatusDict.complete">
                                <img src="@/components/images/agriculturalList/time.png" alt="">
                                <div>完成时间：{{ item.actualEndTime }}</div>
                            </div>
                            <div class="agricultural-item-row" v-if="item.operateStatus === agriculturalStatusDict.execute || item.operateStatus === agriculturalStatusDict.complete">
                                <img src="@/components/images/taeGreenItem/location.png" alt="">
                                <div>执行定位：{{ item.address }}</div>
                            </div>
                            <div class="agricultural-item-row" v-if="item.operateStatus === agriculturalStatusDict.execute || item.operateStatus === agriculturalStatusDict.complete">
                                <img src="@/components/images/agriculturalList/code.png" alt="">
                                <div>茶园码：</div>
                                <FcCodeToQRCode :code="`https://zhiyun.cn88555.com/frontend/weixin/code?code=${decodeURI(item.teaGardenId)}`"  @click="previewCodeImage"></FcCodeToQRCode>
                                <!-- 农事状态为执行中或已完成且农事类型为茶青采摘 -->
                                <div class="agricultural-item-vesselCode" v-if="(item.operateStatus === agriculturalStatusDict.execute || item.operateStatus === agriculturalStatusDict.complete) && item.farmingType === farmingTypeDict.pickTeaGreen">容器码：</div>
                            

                                <FcCodeToQRCode v-if="(item.operateStatus === agriculturalStatusDict.execute || item.operateStatus === agriculturalStatusDict.complete) && item.farmingType === farmingTypeDict.pickTeaGreen" :code="`https://zhiyun.cn88555.com/frontend/weixin/code?code=${decodeURI(item.containerId)}`"  @click="previewCodeImage"></FcCodeToQRCode>
                            </div>
                            <div class="agricultural-item-row" v-if="item.operateStatus === agriculturalStatusDict.complete">
                                <img src="@/components/images/agriculturalList/workImg.png" alt="">
                                <div>工作图片：</div>
                                <div class="agricultural-item-img-list" v-if="Array.isArray(item.imgPathList) && item.imgPathList.length">
                                    <img class="agricultural-item-workImg" v-lazy="workImg" v-for="(workImg, workImgKey) in item.imgPathList" :key="workImgKey" @click="previewWorkImage(workImgKey, item.imgPathList)" />
                                </div>
                                <span style="margin-top: 0.08rem" class="taeGreenItem-qrcode-default" v-else>暂无图片</span>
                            </div>
                            <div class="agricultural-item-operate bgColor" v-if="item.operateStatus === agriculturalStatusDict.waitExecute && isHandle" @click="hanldeExecute(item, key)">
                                <div>扫码执行</div>
                                <img src="@/components/images/agriculturalList/right-white.png" alt="">
                            </div>
                            <div class="agricultural-item-operate bgColor" v-if="item.operateStatus === agriculturalStatusDict.execute && isHandle" @click="handleComplete(item, key)">
                                <div>确认完成</div>
                                <img src="@/components/images/agriculturalList/right-white.png" alt="">
                            </div>

                            <div class="agricultural-item-operate bgColor" v-if="item.operateStatus === agriculturalStatusDict.waitExecute && isEdit" @click="handleEdit(item)">
                                <div>编辑</div>
                                <img src="@/components/images/agriculturalList/right-white.png" alt="">
                            </div>
                        </div>
                    </div>
                    <template #right>
                        <div class="agricultural-item-delete">
                            <img src="@/components/images/agriculturalList/delete.png" alt="">
                        </div>
                    </template>
                </SwipeCell>
            </div>

            <FcEmpty v-if="!list.length && loaded"></FcEmpty>
            <div class="taeGreen-all" v-if="showAll">已加载全部</div>
        </div>
    </div>
</template>

<script>
import { agriculturalStatusDict, farmingTypeDict } from "@/utils/dict"
import { throttleFnc } from '@/utils/common'
import { SwipeCell, Dialog, ImagePreview } from 'vant'
import FcEmpty from '@/components/empty/empty.vue'
import teaGardenCodeImg from "@/components/images/agriculturalList/teaGardenCode.png"
import containerCodeImg from "@/components/images/agriculturalList/containerCode.png"
import FcCodeToQRCode from '@/components/codeToQRcode/codeToQRcode.vue'
export default {
    name: 'agriculturalList',
    components: {
        FcCodeToQRCode,
        SwipeCell,
        FcEmpty
    },
    props: {
        list: {
            type: Array,
            default: () => []
        },
        disabled: {   // 是否禁用滑动
            type: Boolean,
            default: false
        },
        isHandle: {
            type: Boolean,
            default: false
        },
        loaded: {
            type: Boolean,
            default: false
        },
        isEdit: {   // 是否显示编辑按钮
            type: Boolean,
            default: false
        },
        count: {   // 分页总数
            type: Number,
            default: 0
        },
        page: {
            type: Number,
            default: 0
        },
        limit: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            agriculturalStatusDict: agriculturalStatusDict,
            farmingTypeDict: farmingTypeDict,
            teaGardenCodeImg: teaGardenCodeImg,
            containerCodeImg: containerCodeImg,
            key: Math.random()
        }
    },

    computed: {
        /**
            是否显示已加载全部
            @param 
            @return { Boolean } 是否显示已加载全部
        */
        showAll() {
            if (this.count > 0) {
                if (this.loaded && this.count <= this.page * this.limit) {
                    return true
                } else {
                    return false
                }
            } else {
                return false
            }
        }
    },

    methods: {
        /**
            处理滚动事件
            @param { Event } event 滚动事件参数
            @return
        */
        handleScoll: throttleFnc(function (callback, event) {
            if (event.target.scrollTop + event.target.clientHeight >= this.$refs.agriculturalListWrap.clientHeight - 150) {
                // 滚动到底部时触发
                this.$emit('scrolltolower', callback)
            } else {
                callback()
            }
        }),

        /**
            初始化列表
            @param
            @return
        */
        onRefresh() {
            this.key = Math.random()
        },

        /**
            自定义滚动高度
            @param { Number } pageY 滚动至
            @return
        */
        customScrollTo(pageY) {
            this.$refs.agriculturalList.scrollTop = pageY
        },

        /**
            处理点击扫码执行事件
            @param { Object } item 工单项
            @param { Number } key 工单项下标
            @return
        */
        hanldeExecute(item, key) {
            this.$emit('execute', {
                item: item,
                key: key
            })
        },

        /**
            处理点击执行完成事件
            @param { Object } item 工单项
            @param { Number } key 工单项下标
            @return
        */
        handleComplete(item, key) {
            this.$emit('complete', {
                item: item,
                key: key
            })
        },

        /**
            处理点击编辑事件
            @param { Object } item 工单项
            @return
        */
        handleEdit(item) {
            this.$emit('edit', item)
        },

        /**
            自定义滑动单元格关闭时的行为
            @param { Object } object 事件参数对象 
            @param { Object } item 点击的工单项数据 
            @param { Number } key 点击工单项的下标 
            @return
        */
        beforeSwipeCellClose({ name, position, instance }, item, key) {
            switch (position) {
                case 'right':
                    Dialog.confirm({
                        message: '确定删除吗？',
                    }).then(() => {
                        this.$emit('delete', {
                            key: name,
                            item: this.list[name],
                            callback: () => { instance.close() }
                        })

                    });
                    break;
            }
        },

        /**预览码图片
            @param { Image } image 图片地址
            @return
        */
        previewCodeImage(image) {
            ImagePreview([image])
        },

        /** 
             预览工作中图片
            @param { Number } index 点击工作图片下标
            @param { Array } imgList 工作图片列表
            @return
        */
        previewWorkImage(index, imgList) {
            ImagePreview({
                images: imgList,
                startPosition: index
            })
        },

        /** 
             处理点击工单编号二维码事件
            @param { Object } item 工单项数据
            @param { Number } key 工单下标
            @return
        */
        handleClickWorkQRCode(item, key) {
            this.$emit('clickWorkQRCode', {
                ...item,
                workQRCode: this.$refs[`FcCodeToQRCode-${key}`][0].imgSrc
            })
        }


    },

}

</script>

<style scoped lang='scss'>
.agriculturalList {
    overflow-y: auto;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    padding: 0 0.3rem;
    //@at-rootpadding: 0 0.3rem;
    .taeGreen-all {
        margin-bottom: 0.3rem;
    }
}

.agricultural-item {
    overflow: hidden;
    margin-bottom: 0.2rem;
    border-radius: 0.08rem;
    .agricultural-item-box {
        padding-bottom: 0.3rem;
    }
    .agricultural-item-title {
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 1.04rem;
        padding: 0 0.3rem;
        border-bottom: 1px solid #eee;
    }
    .agricultural-item-name {
        color: #151515;
    }

    .agricultural-item-teaGarden {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-left: 0.12rem;
        padding: 0.06rem 0.12rem;
        border-radius: 0.22rem;
        background-color: #eafff8;
        > img {
            width: 0.2rem;
            height: 0.24rem;
        }
        span {
            margin-left: 0.08rem;
            font-size: 0.24rem;
            line-height: 0.32rem;
            zoom: 0.917;
        }
    }

    .agricultural-item-status {
        position: absolute;
        right: 0.3rem;
        top: 0;
        bottom: 0;
        height: 0.44rem;
        width: 1.36rem;
        margin: auto 0;
        > img {
            width: 100%;
            height: 100%;
        }
    }

    .agricultural-item-content {
        overflow: hidden;
        padding: 0.28rem 0.3rem 0;
        .agricultural-item-row {
            position: relative;
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            //flex-wrap: wrap;
            margin-bottom: 0.2rem;
            > img {
                margin-top: 0.05rem;
                width: 0.3rem;
                height: 0.3rem;
            }
            > div {
                position: relative;
                margin-left: 0.16rem;
                font-size: 0.28rem;
                line-height: 0.4rem;
                color: #151515;
            }

            .agricultural-item-QRCode {
                position: absolute;
                right: -0.8rem;
                top: -0.16rem;
                padding: 0.04rem;
            }
            .agricultural-item-codeImg {
                width: 0.72rem;
                height: 0.72rem;
            }
            .agricultural-item-vesselCode {
                margin-left: 0.48rem;
            }
            .agricultural-item-img-list {
                flex: 1;
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
            }
            .agricultural-item-workImg {
                margin-right: 0.2rem;
                margin-bottom: 0.2rem;
                width: 1.2rem;
                height: 0.9rem;
            }
            .agricultural-item-workImg:nth-child(3n) {
                margin-right: 0;
            }
        }
        .agricultural-item-row:last-child {
            margin-bottom: 0;
        }
        .agricultural-item-operate {
            float: right;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            height: 0.56rem;
            width: 1.56rem;
            border-radius: 0.08rem;
            > div {
                font-size: 0.26rem;
                line-height: 0.56rem;
                color: #fff;
            }
            > img {
                margin-top: -0.04rem;
                margin-left: 0.08rem;
                width: 0.12rem;
                height: 0.22rem;
            }
        }
    }
}

.agricultural-item-delete {
    width: 1.04rem;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #cf3723;
    > img {
        width: 0.4rem;
        height: 0.4rem;
    }
}

.agriculturalList-default {
    display: flex;
    flex-direction: column;
    align-items: center;
    > img {
        display: block;
        margin: 0.6rem auto 0;
        width: 3.74rem;
        height: 2.8rem;
    }
    > span {
        font-size: 0.28rem;
        color: #999;
    }
}
</style>


