<template>
    <FcPage title="农事执行" class="agriculturalPage">
        <div class="agricultural">
            <div class="agricultural-top">
                <FcActionSheet placeholder="筛选农事类型" :options="farmingTypeOptions" title="农事类型" v-model="farmingTypeVlaue" @change="handleFarmingTypeChange"></FcActionSheet>
            </div>
            <FcAgriculturalTab :renderList="agriculturalRenderTabList" v-model="activeTabType" @change="handleChangeTab" :totalData="totalData"></FcAgriculturalTab>

            <div class="agricultural-box">
                <FcAgriculturalList :page="currPage" :limit="limit" :count="count" disabled isHandle ref="fcAgriculturalList" :loaded="loaded" :list="agriculturalList" @scrolltolower="handleScrolltolower" @execute="handleExecute" @complete="handleComplete" @clickWorkQRCode="handleClickWorkQRCode"></FcAgriculturalList>
            </div>
        </div>

        <FcDialog title="执行确认" v-model="isShowNormalWorkDialog" type="confirm" confirmText="开始执行" @confirm="handleNormalWorkConfirm">
            <div class="normalWorkDialog">
                <div>{{ teaGardenData.name }}</div>
                <img :src="teaGardenData.img" alt="">
            </div>
        </FcDialog>

        <FcDialog title="执行确认" v-model="isShowPickWorkDialog" type="confirm" confirmText="开始执行" @confirm="handlePickWorkConfirm">
            <div class="pickWorkDialog">
                <div class="pickWorkCodeItem">
                    <div class="pickWorkCodeItem-name">扫描茶园码
                        <img class="pickWorkCodeItem-asterisk" src="@/assets/staff/home/agricultural/agricultural/asterisk.png" alt="">
                    </div>
                    <div class="pickWorkCodeItem-scan">
                        <img v-if="!teaGardenData.img" class="pickWorkCodeItem-scan-img" src="@/assets/staff/home/agricultural/agricultural/scan.png" alt="" @click="handleScan('teaGardenData')">
                        <div v-else class="pickWorkCodeItem-scan-result">
                            <div>{{ teaGardenData.name }}</div>
                            <img :src="teaGardenData.img" alt="">
                        </div>
                    </div>
                </div>
                <div class="pickWorkCodeItem">
                    <div class="pickWorkCodeItem-name">扫描容器码
                        <img class="pickWorkCodeItem-asterisk" src="@/assets/staff/home/agricultural/agricultural/asterisk.png" alt="">
                    </div>
                    <div class="pickWorkCodeItem-scan">
                        <img v-if="!containerData.img" class="pickWorkCodeItem-scan-img" src="@/assets/staff/home/agricultural/agricultural/scan.png" alt="" @click="handleScan('containerData')">
                        <div v-else class="pickWorkCodeItem-scan-result">
                            <div>{{ containerData.name }}</div>
                            <img :src="containerData.img" alt="">
                        </div>
                    </div>
                </div>
            </div>
        </FcDialog>

        <FcDialog title="完成确认" v-model="isShowCompleteDialog" type="confirm" confirmText="确认完成" @confirm="handleCompleteConfirm" @cancel="handleCompleteCancel">
            <div class="completeWork">
                <div class="completeWork-label">
                    <div>上传图片</div>
                    <img src="@/assets/staff/home/agricultural/agricultural/asterisk.png" alt="">
                </div>
                <div class="completeWork-images">
                    <div class="completeWork-imageItem" v-for="(imageItem, imageKey) in completeImageList" :key="imageKey" @click="previewWorkImage(imageKey)">
                        <div class="completeWork-imageItem-uploading" v-if="imageItem === 'uploading'">
                            <Loading size="24" color="#629B88"></Loading>
                        </div>
                        <div class="completeWork-imageItem-box" v-else>
                            <img class="completeWork-img" :src="imageItem" alt="">
                            <img class="completeWork-img-delete" src="@/assets/staff/home/agricultural/agricultural/delete.png" alt="" @click="handleDeleteImage(imageKey)">
                        </div>

                    </div>
                    <div class="completeWork-upload" v-show="completeImageList[completeImageList.length - 1] !== 'uploading'">
                        <img src="@/assets/staff/home/agricultural/agricultural/upload.png" alt="">
                        <input type="file" accept="image/*" capture="environment" @change="handleUploadImg">
                    </div>
                </div>
                <div class="completeWork-prompt">支持上传多张照片</div>
            </div>
        </FcDialog>

        <FcDialog v-model="isShowWorkQRcode" :title="`采摘执行成功`">
            <div class="agricultural-workQRCode">
                <div class="agricultural-workQRCode-title">工单二维码</div>
                <div class="agricultural-workQRCode-title">{{ workData.workNo }}</div>
                <img :src="workData.workQRCode" alt="">
                <div class="agricultural-workQRCode-subTitle">茶青交易时需出示该码，请截图保存</div>

                <div class="agricultural-workQRCode-settle">
                    <img v-if="workData.isSettle == settleStatusDict.unsettled" src="@/assets/staff/home/agricultural/agricultural/unsettled.png" alt="">
                    <img v-if="workData.isSettle == settleStatusDict.settled" src="@/assets/staff/home/agricultural/agricultural/settled.png" alt="">
                </div>
            </div>
        </FcDialog>

        <FcLoading :isShow="showLoading" text="正在获取位置信息..."></FcLoading>
    </FcPage>
</template>

<script>
import { getWeChatQrcode, getWeChatSignData, getLocation } from '@/utils/wechat'
import { Loading, ImagePreview } from 'vant'
import FcAgriculturalTab from '@/components/agriculturalTab/agriculturalTab.vue'
import FcActionSheet from '@/components/actionSheet/actionSheet.vue'
import FcLoading from "@/components/loading/loading.vue"
import { getAgriculturalRenderTabList } from './config'
import FcAgriculturalList from '@/components/agriculturalList/agriculturalList.vue'
import { staffUserDatatorage } from '@/utils/storage'
import { farmingTypeMap } from '@/utils/config'
import { farmingTypeDict, agriculturalStatusDict, settleStatusDict } from '@/utils/dict'
import { farmingTypeOptions } from '@/utils/config'
import { isEmpty, getLinkQuery } from '@/utils/common'
import QRCode from 'qrcode'
import dayjs from 'dayjs'
export default {
    name: 'agricultural',
    components: {
        Loading,
        FcLoading,
        FcAgriculturalList,
        FcAgriculturalTab,
        FcActionSheet
    },

    data() {
        return {
            isResult: false,
            isShowNormalWorkDialog: false,
            isShowPickWorkDialog: false,
            isShowCompleteDialog: false,
            agriculturalRenderTabList: getAgriculturalRenderTabList(),
            activeTabType: 4,
            completeImageList: [],
            agriculturalList: [],
            userData: staffUserDatatorage.getUserData(),
            teaGardenData: {},
            containerData: {},
            totalData: {},
            farmingTypeOptions: [{ text: '全部', value: "-1" }].concat(farmingTypeOptions),
            farmingTypeVlaue: "-1",
            count: 0,
            page: 1,
            currPage: 0,
            limit: 15,
            loaded: false,
            activeAgriculturalData: {},
            settleStatusDict: settleStatusDict,
            activeAgriculturalKey: -1,
            isReadyWeChat: false,
            workData: {},
            isShowWorkQRcode: false,
            showLoading: false
        }
    },
    created() {
        this.initWeChat()
        if (this.$route.params.type) {
            this.activeTabType = this.$route.params.type
        }
        // const result = {
        //     resultStr: 'https://zhiyun.cn88555.com/frontend/weixin/code?code=88.555.1%2F2023040400003&vc=810240'
        // }
        // const qrcode = result.resultStr, queryData = getLinkQuery(qrcode)
        // if (queryData.code) {
        //     const code = decodeURIComponent(queryData.code)

        // } else {
        //     this.$toast("茶园码不正确")

        // }

    },
    mounted() {
        this.getAgriculturalList()
        this.getAgriculturalTotalData()
    },

    methods: {
        /**
            初始化微信sdk
            @param
            @return
        */
        initWeChat() {
            return new Promise((resolve, reject) => {
                getWeChatSignData(["scanQRCode", "getLocation"]).then(() => {
                    this.isReadyWeChat = true
                    resolve()
                }).catch(() => {
                    reject()
                })
            })
        },
        /**
            获取农事列表统计数据
            @param 
            @return
        */
        getAgriculturalTotalData() {
            const params = {
                tenantId: this.userData.tenantId,
                operatePerson: this.userData.userId
            }
            if (this.farmingTypeVlaue != -1) {
                params.farmingType = this.farmingTypeVlaue
            }
            this.$axios({
                url: '/teaAction/getCountByUserId',
                data: params
            }).then((res) => {
                if (!isEmpty(res.data)) {
                    res.data['-1'] = res.data['0'] + res.data['1'] + res.data['2'] + res.data['3']
                    this.totalData = res.data
                } else {
                    this.totalData = {}
                }
            }).catch(() => {
                this.totalData = {}
            })
        },
        /**
            获取农事列表
            @param
            @return
        */
        getAgriculturalList(callback) {
            this.loaded = false
            const params = {
                page: this.page,
                limit: this.limit,
                operatePerson: this.userData.userId,
            }
            if (this.activeTabType != -1) {
                params.operateStatus = this.activeTabType
            }
            if (this.farmingTypeVlaue != -1) {
                params.farmingType = this.farmingTypeVlaue
            }
            this.$axios({
                url: '/teaAction/getFarmingList',
                data: params
            }).then((res) => {
                if (Array.isArray(res.data) && res.data.length) {
                    const agriculturalList = res.data.map((item, key) => {

                        return {
                            ...item,
                            workCodeUrl: `${location.origin}/#/enterprise/teaDeal/add?workId=${item.id}`,
                            farmingName: farmingTypeMap[item.farmingType]
                        }
                    })
                    if (this.page == 1) {
                        this.agriculturalList = agriculturalList
                    } else {
                        this.agriculturalList = this.agriculturalList.concat(agriculturalList)
                    }

                } else {
                    if (this.page == 1) {
                        this.agriculturalList = []
                    }
                }
                this.currPage = this.page
                this.count = res.count || 0
                this.loaded = true
                callback && typeof callback === 'function' && callback()
            }).catch(() => {
                this.loaded = true
                if (this.page == 1) {
                    this.agriculturalList = []
                }
                this.count = 0
                callback && typeof callback === 'function' && callback()
            })
        },
        /**
            处理tab选中项改变事件
            @param
            @return
        */
        handleChangeTab() {
            this.page = 1
            const fcAgriculturalListEle = this.$refs.fcAgriculturalList
            fcAgriculturalListEle.onRefresh && typeof fcAgriculturalListEle.onRefresh === 'function' && fcAgriculturalListEle.onRefresh()
            this.getAgriculturalList()
        },

        /**
           处理农事类型改变事件
           @param 
           @return
       */
        handleFarmingTypeChange() {
            this.page = 1
            const fcAgriculturalListEle = this.$refs.fcAgriculturalList
            fcAgriculturalListEle.onRefresh && typeof fcAgriculturalListEle.onRefresh === 'function' && fcAgriculturalListEle.onRefresh()
            this.getAgriculturalList()
            this.getAgriculturalTotalData()
        },

        /**
            处理滚动到底部事件
            @param { Function } callback 取消节流的回调函数
            @return
        */
        handleScrolltolower(callback) {
            if (this.page * this.limit < this.count) {
                this.page++
                this.getAgriculturalList(callback)
            } else {
                callback()
            }
        },
        /**
            处理点击扫码事件
            @param { String } type 扫码类型
            @return
        */
        handleScan(type) {
            return new Promise((resolve, reject) => {
                if (this.isReadyWeChat) {
                    getWeChatQrcode().then(async (result) => {
                        await this.handleScanResult(type, result)
                        resolve()
                    })
                } else {
                    this.initWeChat().then(() => {
                        getWeChatQrcode().then(async (result) => {
                            await this.handleScanResult(type, result)
                            resolve()
                        })
                    })
                }
            })
        },

        /**
            处理扫码结果
            @param { String } type 扫码类型
            @param { Object } result 扫码结果
            @return
        */
        handleScanResult(type, result) {
            return new Promise((resolve, reject) => {
                const qrcode = result.resultStr, queryData = getLinkQuery(qrcode)
                if (queryData.code) {
                    const code = decodeURIComponent(queryData.code)
                    this.$axios({
                        url: '/scan/code/info',
                        baseUrl: '/zhiyunapi',
                        method: "GET",
                        data: {
                            idisCode: code
                        }
                    }).then((res) => {
                        const resultData = res.data[0]
                        if (type == 'teaGardenData' && resultData.categoryName != "茶园") {
                            this.$toast("茶园码不正确")
                            reject()
                            return
                        }
                        if (type == 'containerData' && resultData.categoryName != "容器") {
                            this.$toast("容器码不正确")
                            reject()
                            return
                        }
                        QRCode.toDataURL(`${qrcode}`).then((url) => {
                            this[type] = {
                                id: resultData.idisCode,
                                name: resultData.productName,
                                img: url
                            }
                            resolve()
                        })
                    }).catch(() => {
                        this.$toast(type == 'teaGardenData' ? "茶园码不正确" : "容器码不正确")
                        reject()
                    })
                } else {
                    this.$toast(type == 'teaGardenData' ? "茶园码不正确" : "容器码不正确")
                    reject()
                }
            })

        },

        /**
            处理点击扫码执行事件
            @param { Object } event 子组件传递的事件参数
            @return
        */
        handleExecute({ item, key }) {
            if (item.farmingType == farmingTypeDict.pickTeaGreen) {
                this.isShowPickWorkDialog = true
            } else {
                this.handleScan("teaGardenData").then(() => {
                    this.isShowNormalWorkDialog = true
                })
            }
            this.activeAgriculturalData = item
            this.activeAgriculturalKey = key
        },

        /**
            处理点击执行完成事件
            @param { Object } event 子组件传递的事件参数
            @return
        */
        handleComplete({ item, key }) {
            this.activeAgriculturalData = item
            this.activeAgriculturalKey = key
            this.isShowCompleteDialog = true
        },


        /**
            获取用户定位
            @param
            @return
        */
        getUserLocation() {
            this.showLoading = true
            return new Promise((resolve, reject) => {
                if (!this.isReadyWeChat) {
                    this.initWeChat().then(() => {
                        this.handleGetLocation().then((address) => {
                            resolve(address)
                        }).catch(() => {
                            reject()
                        })
                    }).catch(() => {
                        reject()
                    })
                } else {
                    this.handleGetLocation().then((address) => {
                        resolve(address)
                    }).catch(() => {
                        reject()
                    })
                }
            })



        },

        /**
            处理获取地理职位的方法
            @param 
            @return
        */
        handleGetLocation() {
            return new Promise((resolve, reject) => {
                getLocation().then((res) => {
                    this.$axios({
                        url: 'https://restapi.amap.com/v3/geocode/regeo',
                        method: "GET",
                        data: {
                            key: 'f751aff38707f53f57b61405db8abf0a',
                            location: `${res.longitude},${res.latitude}`,
                            radius: 50
                        },
                        notCheck: true
                    }).then((locationData) => {
                        resolve(locationData.regeocode.formatted_address)
                    }).catch(() => {
                        reject()
                    })
                }).catch(() => {
                    reject()
                })
            })
        },

        /**
            处理默认农事弹框点击确认事件
            @param
            @return
        */
        handleNormalWorkConfirm() {
            this.getUserLocation().then((address) => {
                this.showLoading = false
                const { id, assignPerson, operatePerson, } = this.activeAgriculturalData
                this.$axios({
                    url: '/teaAction/editFarming',
                    data: {
                        id: id,
                        assignPerson: assignPerson,
                        operatePerson: operatePerson,
                        operateStatus: agriculturalStatusDict.execute,
                        actualStartTime: dayjs(new Date()).format('YYYY-MM-DD HH:mm:ss'),
                        teaGardenId: this.teaGardenData.id,
                        address: address
                    }
                }).then((res) => {
                    // const agriculturalItem = { ...this.agriculturalList[this.activeAgriculturalKey] }
                    // agriculturalItem.teaGardenId = this.teaGardenData.id
                    // agriculturalItem.containerId = this.containerData.id
                    // agriculturalItem.operateStatus = agriculturalStatusDict.execute
                    // this.$set(this.agriculturalList, this.activeAgriculturalKey, agriculturalItem)
                    this.page = 1
                    this.getAgriculturalList()
                    this.teaGardenData = {}
                    this.$toast("操作成功")
                    this.isShowNormalWorkDialog = false
                })
            }).catch(() => {
                this.showLoading = false
                this.$toast("获取地理位置失败")
            })

        },

        /**
            处理茶青采摘农事弹框点击确认事件
            @param
            @return
        */
        handlePickWorkConfirm() {
            this.getUserLocation().then((address) => {
                this.showLoading = false
                const { id, assignPerson, operatePerson, } = this.activeAgriculturalData
                this.$axios({
                    url: '/teaAction/editFarming',
                    data: {
                        id: id,
                        assignPerson: assignPerson,
                        operatePerson: operatePerson,
                        operateStatus: agriculturalStatusDict.execute,
                        actualStartTime: dayjs(new Date()).format('YYYY-MM-DD HH:mm:ss'),
                        teaGardenId: this.teaGardenData.id,
                        containerId: this.containerData.id,
                        address: address
                    }
                }).then((res) => {
                    // const agriculturalItem = { ...this.agriculturalList[this.activeAgriculturalKey] }
                    // agriculturalItem.teaGardenId = this.teaGardenData.id
                    // agriculturalItem.containerId = this.containerData.id
                    // agriculturalItem.operateStatus = agriculturalStatusDict.execute
                    // this.$set(this.agriculturalList, this.activeAgriculturalKey, agriculturalItem)
                    this.page = 1
                    this.getAgriculturalList()
                    this.teaGardenData = {}
                    this.containerData = {}
                    this.$toast("操作成功")
                    this.isShowPickWorkDialog = false
                })
            }).catch(() => {
                this.showLoading = false
                this.$toast("获取地理位置失败")
            })
        },

        /**
            处理点击工单二维码事件
            @param { Object } workData 工单数据
            @return
        */
        handleClickWorkQRCode(workData) {
            this.workData = workData
            this.isShowWorkQRcode = true
        },

        /**
            处理完成确认弹框点击取消事件
            @param
            @return
        */
        handleCompleteCancel() {
            this.completeImageList = []
        },

        /**
            处理完成确认弹框点击确认事件
            @param
            @return
        */
        handleCompleteConfirm() {
            if (this.completeImageList.length) {
                for (let i = 0; i < this.completeImageList.length; i++) {
                    if (this.completeImageList[i] === 'uploading') {
                        this.$toast('请等待图片上传结束')
                        return
                    }
                }
                const { id, farmingType, workNo } = this.activeAgriculturalData
                this.$axios({
                    url: '/teaAction/editConfirmFarming',
                    data: {
                        workId: id,
                        //actualEndTime: dayjs(new Date()).format('YYYY-MM-DD HH:mm:ss'),
                        imgPathList: this.completeImageList
                    }
                }).then((res) => {
                    this.$toast('操作成功')
                    this.page = 1
                    this.getAgriculturalList()
                    this.completeImageList = []
                    this.isShowCompleteDialog = false
                    if (farmingType === farmingTypeDict.pickTeaGreen) {
                        QRCode.toDataURL(`${location.origin}/#/enterprise/teaDeal/add?workId=${id}`).then((url) => {
                            this.workData = {
                                workNo: workNo,
                                workQRCode: url,
                                isSettle: settleStatusDict.unsettled
                            }
                            this.isShowWorkQRcode = true
                        })
                    }
                })
            } else {
                this.$toast('请上传工作图片')
            }
        },


        /**
            处理上传图片
            @param { Event } event 上传图片事件
            @return
        */
        handleUploadImg(event) {
            let formData = new FormData()
            formData.append('file', event.target.files[0])
            event.srcElement.value = ""
            this.completeImageList.push('uploading')
            this.$axios({
                url: '/api/v1/common/uploadImg',
                data: formData,
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            }).then((res) => {
                this.$set(this.completeImageList, this.completeImageList.length - 1, res.data)
                this.$toast('上传成功')
            })
        },

        /**
            处理删除上传图片事件
            @param { Number } index 当前点击的图片下标
            @return
        */
        handleDeleteImage(index) {
            this.completeImageList.splice(index, 1)
        },

        /**
            处理预览工作图片
            @param { Number } index 当前点击的图片下标
            @return
        */
        previewWorkImage(index) {
            const imgList = []
            for (let i = 0; i < this.completeImageList.length; i++) {
                if (this.completeImageList[i] !== 'uploading') {
                    imgList.push(this.completeImageList[i])
                }
            }
            ImagePreview({
                images: imgList,
                startPosition: index
            })
        }
    },

}

</script>

<style scoped lang='scss'>
.agricultural {
    position: relative;
    min-height: 100%;
    background-color: #f5f5f5;
    /deep/ .agricultural-tabs {
        border-top: none;
    }
}

.agriculturalPage {
    /deep/ .van-overlay {
        z-index: 99999;
    }
}

.agricultural-top {
    padding: 0.08rem 0.3rem 0;
    background-color: #fff;
}

.agricultural-box {
    position: absolute;
    top: 3.5rem;
    left: 0rem;
    right: 0rem;
    bottom: 0;
}

.normalWorkDialog {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.2rem 0 0.6rem;
    div {
        font-size: 0.28rem;
        line-height: 0.4rem;
        color: #151515;
    }
    img {
        margin-top: 0.16rem;
        width: 2.52rem;
        height: 2.52rem;
        background-color: #5a96ec;
    }
}

.pickWorkDialog {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0.2rem 0.6rem 0.6rem;
    .pickWorkCodeItem {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        flex: 1;
        .pickWorkCodeItem-name {
            position: relative;
            font-size: 0.28rem;
            line-height: 0.4rem;
            color: #151515;
        }
        .pickWorkCodeItem-scan {
            min-height: 1.28rem;
            .pickWorkCodeItem-scan-img {
                margin-top: 0.32rem;
            }
            img {
                width: 1.28rem;
                height: 1.28rem;
            }
        }
        .pickWorkCodeItem-scan-result {
            margin-top: 0.2rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            div {
                margin-bottom: 0.12rem;
                font-size: 0.26rem;
                line-height: 0.36rem;
                color: rgba(21, 21, 21, 0.6);
            }
        }
        .pickWorkCodeItem-asterisk {
            position: absolute;
            width: 0.12rem;
            height: 0.12rem;
            top: -1px;
            left: -0.14rem;
        }
    }
}

.completeWork {
    position: relative;
    padding: 0.2rem 0.46rem 0.82rem;
    min-height: 3.4rem;
    display: flex;
    flex-direction: row;
    .completeWork-label {
        position: relative;
        div {
            font-size: 0.28rem;
            line-height: 0.4rem;
            color: #151515;
        }
        img {
            position: absolute;
            width: 0.12rem;
            height: 0.12rem;
            top: -1px;
            left: -0.14rem;
        }
    }

    .completeWork-images {
        margin-left: 0.2rem;
        flex: 1;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        > div {
            position: relative;
            margin-right: 0.2rem;
            margin-bottom: 0.32rem;
            width: 1.2rem;
            height: 0.9rem;
            .completeWork-img {
                width: 1.2rem;
                height: 0.9rem;
            }
            .completeWork-img-delete {
                position: absolute;
                top: -0.18rem;
                right: -0.18rem;
                width: 0.36rem;
                height: 0.36rem;
            }
        }
        > div:nth-child(3n) {
            margin-right: 0;
        }

        .completeWork-upload {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 1.2rem;
            height: 0.9rem;
            background-color: #f1f6ff;
            img {
                width: 0.4rem;
                height: 0.36rem;
            }
            > input {
                z-index: 2;
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                opacity: 0;
            }
        }

        .completeWork-imageItem-uploading {
            width: 1.2rem;
            height: 0.9rem;
            background-color: #f1f6ff;
            /deep/ .van-loading {
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                .van-loading__spinner {
                    width: 0.48rem;
                    height: 0.48rem;
                }
                .van-loading__text {
                    margin-top: 0.1rem;
                    font-size: 0.24rem;
                }
            }
        }
    }

    .completeWork-prompt {
        position: absolute;
        bottom: 0.4rem;
        left: 0;
        right: 0;
        text-align: center;
        font-size: 0.24rem;
        line-height: 0.34rem;
        color: rgba(21, 21, 21, 0.5);
    }
}

.agricultural-workQRCode {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.24rem 0 0.7rem;
    .agricultural-workQRCode-title {
        font-size: 0.28rem;
        line-height: 0.4rem;
        color: #151515;
    }
    img {
        width: 3.5rem;
        height: 3.5rem;
    }
    .agricultural-workQRCode-subTitle {
        font-size: 0.24rem;
        line-height: 0.34rem;
        color: rgba(21, 21, 21, 0.5);
    }

    .agricultural-workQRCode-settle {
        position: absolute;
        top: -0.88rem;
        right: 0.14rem;
        width: 1.04rem;
        height: 1.54rem;
        > img {
            width: 100%;
            height: 100%;
        }
    }
}
</style>


